import React, { Component } from 'react';
import { connect } from 'react-redux';
import * as actions from '../state/actions/index';

import InformationTab from '../components/landing-page/InformationTab';
import MonthlyIncome from '../components/landing-page/MonthlyIncome';


class TransactionHistoryApp extends Component {

  componentDidMount() {

    const accessToken = localStorage.getItem("accessToken");
    console.log("from React App " + accessToken);

  }

  render() {
    return (

      <div className="row">
        <div className="col-lg-8">
          <InformationTab />
        </div>
        <div className="col-lg-4">
          <MonthlyIncome />
        </div>
      </div>

    );
  }
}

const mapStateToProps = state => {
  return {
    userProfile: state.transactionDisputeReducer.userProfile,
  }
}
const mapDispatchToProps = dispatch => {
  return {
    remoteGetUserProfile: (accessToken) => dispatch(actions.remoteGetUserProfile(accessToken)),
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(TransactionHistoryApp);



