import React, { Component } from 'react';
import { connect } from 'react-redux';
import * as actions from '../../state/actions/index';



class MonthlyIncome extends Component {

    componentDidMount = () => {
        const accessToken = localStorage.getItem("accessToken");
        // const accessToken = "3c9aaa63105f3b50326dcfeab0df42a5";

        if (accessToken) {
            console.log(accessToken);
            this.props.remoteGetUserProfile(accessToken);
        }

    }
    clickHandeler = () => {
        window.location = 'http://' + window.location.host + '/fsi-credit-card-dispute-customer/en/landing_secure.page';
        //   window.location = 'http://localhost:8181/fsi-credit-card-dispute-customer/en/landing_secure.page';
    }

    render() {
        const userProfile = this.props.userProfile;
        var graph = require('../../css/monthyIncomebg.png');

        if (userProfile.name == 'Jon') {
            graph = require('../../css/monthyIncomebg.png');

        }
        if (userProfile.name == 'Khal') {
            graph = require('../../css/monthyIncomebg2.png');

        }
        if (userProfile.name == 'Arya') {
            graph = require('../../css/monthyIncomebg3.png');
        }
        return (

            <div className="widget-monthly lazur-bg no-padding monthyIncome" style={{ backgroundImage: "url(" + graph + ")", minHeight: '295px' }}>
                <div className="p-m pull-right text-right" style={{ padding:"5%" }}>
                    <h1 className="m-xs">{userProfile.crntBal}</h1>
                    <p style={{ fontSize:"16px" }}>Account Balance</p>
                    <p style={{ fontSize:"16px", paddingTop:"20px" }}>{userProfile.avaCredit}
                        <br />Available Credit</p>
                </div>
                <div className="flot-chart">
                    <div className="flot-chart-content" id="flot-chart2"></div>
                </div>
            </div>
        );
    }
}
const mapStateToProps = state => {
    return {
        userProfile: state.transactionDisputeReducer.userProfile,
    }
}
const mapDispatchToProps = dispatch => {
    return {
        remoteGetUserProfile: (accessToken) => dispatch(actions.remoteGetUserProfile(accessToken)),
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(MonthlyIncome);



